  border-radius: 50%;
  cursor: pointer;
  height: 22px;
  left: 0;
  position: absolute;
  top: 0;
  width: 22px;
}

.round label:after {
  border: 2px solid #fff;
  border-top: none;
  border-right: none;
  content: "";
  height: 6px;
  left: 6.5px;
  opacity: 0;
  position: absolute;
  top: 7.5px;
  transform: rotate(-45deg);
  width: 11px;
}

.round input[type="checkbox"] {
  visibility: hidden;
}

.round input[type="checkbox"]:checked + label {
  background-color: #57bbad;
  border-color: #57bbad;
}

.round input[type="checkbox"]:checked + label:after {
  opacity: 1;
}

